const Ai = require('./ai.js');

module.exports = {
  findBestMove (board,specMoves,depth) {
    let moves = parseMoveClusters(
                  findAllPieces(board,specMoves.currentSide)
                  .map(piece=> findAllLegalMovesByPiece(piece,board,specMoves))
                );
    if (moves.length === 0) return {checkmate: true, side: specMoves.currentSide};

    let bestMove = null;
    moves.forEach(move => {
      let currentNode = new BoardNode(move,board,specMoves,depth);
      if (currentNode.checkmate) {
        return {move: currentNode.move, checkmate: true};
      }
      bestMove = bestMove ?
        (currentNode.score > bestMove.score ?
          currentNode : bestMove) : currentNode;
    });
    return {move: bestMove.move};
  },

  findAllPieces (board,color) {
    let returnPieces = [];
    board.forEach((row,i)=>{
      row.forEach((square,j)=>{
        let testSquare = parseSquare(board,i,j);
        if (testSquare.side === color) {
          returnPieces.push(testSquare);
        }
      });
    });
    return returnPieces;
  },

  findAllLegalMovesByPiece (piece, board, specMoves) {
    let allSquares = [];
    let allMoves;
    for (let i=0;i<8;i++) {
      for (let j=0;j<8;j++) {
        allSquares.push([j,i]);
      }
    }
    if (piece.type === 'r') {
      allMoves = allSquares.filter(square=> {
        return rookMoves(piece.pos, square[0], square[1]);
      }).filter(square=> {
        return canMove(square[0], square[1], board, piece.pos,specMoves);
      }).map(move => [piece.pos,move]);
    }

    if (piece.type === 'n') {
      allMoves = allSquares.filter(square=> {
        return knightMoves(piece.pos, square[0], square[1]);
      }).filter(square=> {
        return canMove(square[0], square[1], board, piece.pos,specMoves);
      }).map(move => [piece.pos,move]);
    }

    if (piece.type === 'b') {
      allMoves = allSquares.filter(square=> {
        return bishopMoves(piece.pos, square[0], square[1]);
      }).filter(square=> {
        return canMove(square[0], square[1], board, piece.pos,specMoves);
      }).map(move => [piece.pos,move]);
    }

    if (piece.type === 'q') {
      allMoves = allSquares.filter(square=> {
        return queenMoves(piece.pos, square[0], square[1]);
      }).filter(square=> {
        return canMove(square[0], square[1], board, piece.pos,specMoves);
      }).map(move => [piece.pos,move]);
    }

    if (piece.type === 'p') {
      allMoves = allSquares.filter(square=> {
        return pawnMoves(square[0], square[1], piece.pos, board);
      }).filter(square=> {
        return canMove(square[0], square[1], board, piece.pos,specMoves);
      }).map(move => [piece.pos,move]);
    }

    if (piece.type === 'k') {
      allMoves = allSquares.filter(square=> {
        return kingMoves(piece.pos, square[0], square[1]);
      }).filter(square=> {
        return canMove(square[0], square[1], board, piece.pos,specMoves);
      }).map(move => [piece.pos,move]);
    }
    return allMoves;
  },

  parseMoveClusters (array) {
    let result = [];
    array.forEach(subArray => {
      subArray.forEach(move => {
        result.push(move);
      });
    });
    return result;
  },

  parseSquare (pieces, y, x) {
    let pieceInfo = {
      side: pieces[y][x][0][0],
      type: pieces[y][x][0][2],
      pos: [x,y],
      defended: false
    };
    return pieceInfo;
  },

  PIECE_VALUES: {
    'p': 100,
    'r': 500,
    'b': 300,
    'n': 300,
    'q': 900,
    'k': 10000
  }
};



